"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Dumbbell, Plus, Trash2, Save, ArrowLeft } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Separator } from "@/components/ui/separator";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import type { Exercise } from "@/db/schema";

interface NewWorkoutClientProps {
  userId: string;
  exercises: Exercise[];
}

interface WorkoutExerciseForm {
  exerciseId: string;
  exercise: Exercise | null;
  restSeconds: number;
  sets: {
    setNumber: number;
    weightLbs?: number;
    reps?: number;
    durationSeconds?: number;
    distanceMiles?: number;
  }[];
}

export default function NewWorkoutClient({ userId, exercises }: NewWorkoutClientProps) {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(false);
  const [title, setTitle] = useState("");
  const [notes, setNotes] = useState("");
  const [selectedExercises, setSelectedExercises] = useState<WorkoutExerciseForm[]>([]);

  // Group exercises by category for better organization
  const exercisesByCategory = exercises.reduce((acc, exercise) => {
    if (!acc[exercise.category]) {
      acc[exercise.category] = [];
    }
    acc[exercise.category].push(exercise);
    return acc;
  }, {} as Record<string, Exercise[]>);

  const handleAddExercise = (exercise: Exercise) => {
    const newExercise: WorkoutExerciseForm = {
      exerciseId: exercise.id,
      exercise,
      restSeconds: 60,
      sets: [{ setNumber: 1 }]
    };
    setSelectedExercises([...selectedExercises, newExercise]);
  };

  const handleRemoveExercise = (index: number) => {
    const updated = selectedExercises.filter((_, i) => i !== index);
    setSelectedExercises(updated);
  };

  const handleAddSet = (exerciseIndex: number) => {
    const updated = [...selectedExercises];
    const currentSets = updated[exerciseIndex].sets;
    const newSetNumber = Math.max(...currentSets.map(s => s.setNumber), 0) + 1;
    updated[exerciseIndex].sets = [...currentSets, { setNumber: newSetNumber }];
    setSelectedExercises(updated);
  };

  const handleRemoveSet = (exerciseIndex: number, setIndex: number) => {
    const updated = [...selectedExercises];
    updated[exerciseIndex].sets = updated[exerciseIndex].sets.filter((_, i) => i !== setIndex);
    setSelectedExercises(updated);
  };

  const handleUpdateSet = (exerciseIndex: number, setIndex: number, field: string, value: string) => {
    const updated = [...selectedExercises];
    const set = updated[exerciseIndex].sets[setIndex];

    if (field === 'weightLbs' || field === 'distanceMiles') {
      set[field] = value ? parseFloat(value) : undefined;
    } else if (field === 'reps' || field === 'durationSeconds') {
      set[field] = value ? parseInt(value) : undefined;
    }

    setSelectedExercises(updated);
  };

  const handleUpdateRest = (exerciseIndex: number, value: string) => {
    const updated = [...selectedExercises];
    updated[exerciseIndex].restSeconds = parseInt(value) || 0;
    setSelectedExercises(updated);
  };

  const handleSaveWorkout = async () => {
    if (!title.trim()) {
      alert("Please enter a workout title");
      return;
    }

    if (selectedExercises.length === 0) {
      alert("Please add at least one exercise");
      return;
    }

    // Validate all exercises have at least one set with data
    const hasValidSets = selectedExercises.some(exercise =>
      exercise.sets.some(set =>
        set.reps || set.durationSeconds || set.weightLbs || set.distanceMiles
      )
    );

    if (!hasValidSets) {
      alert("Please add at least one set with data (reps, duration, weight, or distance)");
      return;
    }

    setIsLoading(true);

    try {
      const response = await fetch('/api/workouts', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          title,
          notes,
          exercises: selectedExercises.map(exercise => ({
            exerciseId: exercise.exerciseId,
            restSeconds: exercise.restSeconds,
            sets: exercise.sets.map(set => ({
              setNumber: set.setNumber,
              weightLbs: set.weightLbs,
              reps: set.reps,
              durationSeconds: set.durationSeconds,
              distanceMiles: set.distanceMiles
            }))
          }))
        })
      });

      if (response.ok) {
        router.push("/dashboard");
      } else {
        const error = await response.text();
        alert(`Failed to save workout: ${error}`);
      }
    } catch (error) {
      alert(`Failed to save workout: ${error}`);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Header */}
        <Card className="mb-6">
          <CardHeader>
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-4">
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => router.push("/dashboard")}
                >
                  <ArrowLeft className="w-4 h-4" />
                  Back to Dashboard
                </Button>
                <CardTitle>Log New Workout</CardTitle>
              </div>
              <Button
                onClick={handleSaveWorkout}
                disabled={isLoading}
              >
                <Save className="w-4 h-4 mr-2" />
                {isLoading ? "Saving..." : "Save Workout"}
              </Button>
            </div>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <Label htmlFor="title">Workout Title *</Label>
                <Input
                  id="title"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  placeholder="e.g., Morning Strength Training"
                  className="mt-1"
                />
              </div>
              <div>
                <Label htmlFor="notes">Notes (Optional)</Label>
                <Textarea
                  id="notes"
                  value={notes}
                  onChange={(e) => setNotes(e.target.value)}
                  placeholder="Any additional notes about this workout..."
                  className="mt-1"
                  rows={3}
                />
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Exercise Selection */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Dumbbell className="w-5 h-5" />
              Add Exercises
            </CardTitle>
            <CardDescription>
              Select exercises to include in your workout
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {Object.entries(exercisesByCategory).map(([category, categoryExercises]) => (
                <div key={category}>
                  <h3 className="font-medium text-sm text-gray-700 dark:text-gray-300 mb-2 capitalize">
                    {category}
                  </h3>
                  <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-2">
                    {categoryExercises.map((exercise) => (
                      <Button
                        key={exercise.id}
                        variant="outline"
                        size="sm"
                        onClick={() => handleAddExercise(exercise)}
                        disabled={selectedExercises.some(e => e.exerciseId === exercise.id)}
                        className="justify-start h-auto p-3 text-left"
                      >
                        <div>
                          <div className="font-medium">{exercise.name}</div>
                          {exercise.description && (
                            <div className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                              {exercise.description}
                            </div>
                          )}
                        </div>
                      </Button>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* Selected Exercises */}
        {selectedExercises.length > 0 && (
          <div className="space-y-4">
            <h2 className="text-xl font-semibold">Selected Exercises</h2>
            {selectedExercises.map((workoutExercise, exerciseIndex) => (
              <Card key={exerciseIndex}>
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      <CardTitle className="text-lg">
                        {workoutExercise.exercise?.name}
                      </CardTitle>
                      <Badge variant="secondary">
                        {workoutExercise.exercise?.category}
                      </Badge>
                    </div>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => handleRemoveExercise(exerciseIndex)}
                    >
                      <Trash2 className="w-4 h-4" />
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {/* Rest Period */}
                    <div>
                      <Label>Rest Period (seconds)</Label>
                      <Input
                        type="number"
                        value={workoutExercise.restSeconds}
                        onChange={(e) => handleUpdateRest(exerciseIndex, e.target.value)}
                        min="0"
                        className="mt-1 w-32"
                      />
                    </div>

                    {/* Sets */}
                    <div>
                      <div className="flex items-center justify-between mb-3">
                        <Label>Sets</Label>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleAddSet(exerciseIndex)}
                        >
                          <Plus className="w-4 h-4 mr-1" />
                          Add Set
                        </Button>
                      </div>

                      <div className="space-y-3">
                        {workoutExercise.sets.map((set, setIndex) => (
                          <Card key={setIndex} className="bg-gray-50 dark:bg-gray-800">
                            <CardContent className="pt-4">
                              <div className="flex items-center justify-between mb-3">
                                <span className="font-medium">Set {set.setNumber}</span>
                                {workoutExercise.sets.length > 1 && (
                                  <Button
                                    variant="ghost"
                                    size="sm"
                                    onClick={() => handleRemoveSet(exerciseIndex, setIndex)}
                                  >
                                    <Trash2 className="w-4 h-4" />
                                  </Button>
                                )}
                              </div>

                              <div className="grid grid-cols-2 sm:grid-cols-4 gap-3">
                                {workoutExercise.exercise?.category === 'strength' ? (
                                  <>
                                    <div>
                                      <Label>Weight (lbs)</Label>
                                      <Input
                                        type="number"
                                        value={set.weightLbs || ''}
                                        onChange={(e) => handleUpdateSet(exerciseIndex, setIndex, 'weightLbs', e.target.value)}
                                        placeholder="0"
                                        step="0.5"
                                      />
                                    </div>
                                    <div>
                                      <Label>Reps</Label>
                                      <Input
                                        type="number"
                                        value={set.reps || ''}
                                        onChange={(e) => handleUpdateSet(exerciseIndex, setIndex, 'reps', e.target.value)}
                                        placeholder="0"
                                        min="0"
                                      />
                                    </div>
                                  </>
                                ) : (
                                  <>
                                    <div>
                                      <Label>Duration (seconds)</Label>
                                      <Input
                                        type="number"
                                        value={set.durationSeconds || ''}
                                        onChange={(e) => handleUpdateSet(exerciseIndex, setIndex, 'durationSeconds', e.target.value)}
                                        placeholder="0"
                                        min="0"
                                      />
                                    </div>
                                    <div>
                                      <Label>Distance (miles)</Label>
                                      <Input
                                        type="number"
                                        value={set.distanceMiles || ''}
                                        onChange={(e) => handleUpdateSet(exerciseIndex, setIndex, 'distanceMiles', e.target.value)}
                                        placeholder="0"
                                        step="0.1"
                                      />
                                    </div>
                                  </>
                                )}
                              </div>
                            </CardContent>
                          </Card>
                        ))}
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}